.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  justify-content: center;
  padding-top: 20px;

}

* {
  box-sizing: border-box;
}

.full-search input {
  padding: 1.5rem 4rem;
  width: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.photo-card {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  border-radius: 3%;
}

.info {
  display: flex;
  gap: 10px;
  place-content: center;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search-bar {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
  justify-content: center;
  background-color: #b8aed1;
}

@media screen and (max-width: 480px) {

  .search-bar {
    padding-left: 10px;
    padding-right: 24px;
  }
}

input {
  font-family: 'Roboto', sans-serif;
  color: #333;
  font-size: 1.2rem;
  margin: 0 auto;
  padding: 0.5rem 2rem;
  border-radius: 0.8rem;
  background-color: rgb(255, 255, 255);
  border: none;
  width: 90%;
  display: block;
  border-bottom: 0.3rem solid transparent;
}

.search-form {
  display: flex;
  gap: 20px;
  height: 50px;
}

.black-bt {
  all: unset;
  width: 100px;
  height: 30px;
  font-size: 16px;
  background: transparent;
  border: none;
  position: relative;
  color: #f0f0f0;
  cursor: pointer;
  z-index: 1;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.black-bt::after,
.black-bt::before {
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: -99999;
  transition: all .4s;
}

.black-bt::before {
  transform: translate(0%, 0%);
  width: 100%;
  height: 100%;
  background: #28282d;
  border-radius: 10px;
}

.black-bt::after {
  transform: translate(10px, 10px);
  width: 35px;
  height: 35px;
  background: #ffffff15;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 50px;
}

.black-bt:hover::before {
  transform: translate(5%, 20%);
  width: 110%;
  height: 110%;
}

.black-bt:hover::after {
  border-radius: 10px;
  transform: translate(0, 0);
  width: 100%;
  height: 100%;
}

.black-bt:active::after {
  transition: 0s;
  transform: translate(0, 5%);
}


.load-more {
  display: flex;
  margin: 0 auto;
  margin-top: 30px;
  margin-bottom: 30px;
}

.hide {
  display: none;
}

a {
  text-decoration: none;
  color: #212121;
}

.photo-card {
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
}

.photo-card:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.full-search {
  height: 100vh;
  align-items: center;
  padding-top: 0;
  padding-bottom: 0;
}

.full-search form {
  flex-direction: column;
  align-items: center;
}

html {
  scroll-behavior: smooth;
}

.hidden {
  display: none;
}

.back-to-top-icon {
  width: 1rem;
  height: 1rem;
  color: black;
}

.back-to-top {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  border-radius: 100%;
  background: #141c38;
  padding: 1rem;
  border: none;
  cursor: pointer;

  align-items: center;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: transparent 0 0 0 3px, rgba(18, 18, 18, .1) 0 6px 20px;
  box-sizing: border-box;
  color: #121212;
  cursor: pointer;
}

.back-to-top-icon {
  width: 2rem;
  height: 2rem;
  color: #000000;
}

.back-to-top:hover {
  opacity: 60%;
}

.back-to-top:hover {
  box-shadow: #121212 0 0 0 3px, transparent 0 0 0 0;
}

.sorting {
  display: flex;
  left: 3%;
  margin-top: 20px;
  position: relative;
  margin-left: 11%;
}

select {
    position: relative;
    width: 200px;

}

label {
  position: relative;
  display: inline-block;
  margin-right: 1rem;
}
  label:before {
    content: '';
    position: absolute;
    right: 7px;
    top: 3px;
    width: 22px;

    background: -webkit-linear-gradient(#fff, #f0f0f0);
    background: -moz-linear-gradient(#fff, #f0f0f0);
    background: linear-gradient(#f5f5f5, #e0e0e0);
    background: #fff; 

    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    pointer-events: none;
    display: block;
  }

  label:after {
    content: " ";
    position: absolute;
    right: 15px;
    top: 60%;
    /* margin-top: -3px; */
    z-index: 2;
    pointer-events: none;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 6.9px 4px 0 4px;
    border-color: #aaa transparent transparent transparent;
    pointer-events: none;
  }

  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    padding: 10px 10px 10px 10px;

    border: 1px solid #e0e0e0;
    border-radius: 3px;
    box-shadow: inset 1px 1px 1px 0px rgba(0, 0, 0, 0.2);
    background: #fff;

    min-width: 150px;
}

select::-ms-expand {
  display: none;
}

.save {
  position: absolute;
  left: 10px;
  top: 10px;
  cursor: pointer;

  padding: 5px 10px 5px 10px;
  background-color: #b8aed1;
  border: none;
}

.saved {
  background-color: coral;
  color: #fafafa;
}